<template id="me">
	<div class="me_box">
		<div class="me_top">
			<router-link to="/me/personal" class="me1">
				<img class="me1_left" src="../images/13.jpg"/>
				<div class="me1_right">
					<div class="perfec_top">欧阳</div>
					<div class="perfec">
						<span>完善资料</span>
						<img class="edit" src="../images/edit.png"/>
					</div>
				</div>
				<img class="me1_back" src="../images/back.png"/>
			</router-link>
		</div>
		<router-link to = "/me/moodCount" class="me_lists ">
			<div class="me_list">
				<img class="listimg1" src="../images/listimg1.png"/>
				<div class="listtext">我的心情指数</div>
				<img class="listimg2" src="../images/back.png" />
			</div>
		</router-link>
		<router-link to="/me/friendsCount" class="me_lists">
			<div class="me_list">
				<img class="listimg1" src="../images/listimg2.png"/>
				<div class="listtext">我的好友</div>
				<img class="listimg2" src="../images/back.png"/>
			</div>
		</router-link>
		<router-link to="/me/remind" class="me_lists">
			<div class="me_list">
				<img class="listimg1" src="../images/listimg3.png"/>
				<div class="listtext">心情记录提醒设置</div>
				<img class="listimg2" src="../images/back.png"/>
			</div>
		</router-link>
		<router-link to="/me/privacy" class="me_lists">
			<div class="me_list">
				<img class="listimg1" src="../images/listimg4.png"/>
				<div class="listtext">隐私设置</div>
				<img class="listimg2" src="../images/back.png"/>
			</div>
		</router-link>
		<router-link to="/me/problem" class="me_lists">
			<div class="me_list">
				<img class="listimg1" src="../images/listimg5.png"/>
				<div class="listtext">常见问题Q&A</div>
				<img class="listimg2" src="../images/back.png"/>
			</div>
		</router-link>
		<router-link to="/me/proposal" class="me_lists">
			<div class="me_list">
				<img class="listimg1" src="../images/listimg6.png"/>
				<div class="listtext">意见反馈</div>
				<img class="listimg2" src="../images/back.png"/>
			</div>
		</router-link>
		<a class="me_bottom weui-btn weui-btn_primary" url="">生成邀请卡</a>
	</div>
</template>
<style>
	.me_top{
		background:#e5e5e5;
		padding: 15px 0;
	}
	.me1_left{
		height:60px;
		width:60px;
		display: block;
		border-radius: 5px;
		float: left;
	}
	.me1{
		background:#fff;
		padding:15px;
		height:60px;
		display: block;
	}
	.me1_right{
		float: left;
		margin-left: 14px;
	}
	.perfec_top{
		color: #333333;
		font-size:14px;
		margin-bottom:12px;
	}
	.perfec{
		font-size:12px;
		color: #666666;
		position: relative;
	}
	.perfec img{
		position: absolute;
		top:-1px;
		left: 55px;
	}
	.edit{
		height:20px;
		width:20px;
	}
	.me1_back{
		display: block;
		float: right;
		width:20px;
		height:20px;
		transform: rotate(180deg);
		margin-top:20px;
	}
	.me_lists{
		background:#fff;
		padding:0 15px;
		display: block;
	}
	.me_list{
		height:60px;
		border-bottom:1px solid #e5e5e5;
		line-height: 60px;
	}
	.listimg1{
		width:30px;
		height:30px;
		display: block;
		float: left;
		margin-top: 16px;
	}
	.listimg2{
		display: block;
		float: right;
		width:20px;
		height:20px;
		transform: rotate(180deg);
		margin-top:20px;
	}
	.listtext{
		font-size:15px;
		color: #333333;
		float: left;
		margin-left:15px;
	}
	.me_bottom{
		width:90%;
		margin:30px auto;
	}
</style>
<script type="text/javascript">
	var me={
		template:'#me'
	}
	module.exports=me
</script>



